/* media for mobile */
@media (max-width: 1024px) {
  :root {
    font-size: 7px;
  }
  .verse {
    font-size: 1.5rem;
  }
}

/* media for tablet */
@media (min-width: 1024px) and (max-width: 1366px) {
  :root {
    font-size: 10px;
  }
  .verse {
    font-size: 2rem;
  }
}

/* media for desktop */
@media (min-width: 1366px) {
  :root {
    font-size: 12px;
  }
  .verse {
    font-size: 2.5rem;
  }
}

/* media for large desktop */
@media (min-width: 1920px) {
  :root {
    font-size: 14px;
  }
  .verse {
    font-size: 3rem;
  }
}